import React from "react";

class Detail extends React.Component {
  componentDidMount() {
    const {
      location: { state },
      history,
    } = this.props;
    console.log(state);

    if (state === undefined) {
      history.push("/");
    }
  }
  render() {
    const { location } = this.props;
    console.log(this.props);
    if (location.state) {
      return (
        <div>
          <span>{location.state.title}</span>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default Detail;
